.auth-container {
  .auth-block {
    margin-top: 30px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 48px 56px;
    max-width: 528px;
    background: #ffffff;
    border-radius: 16px;
    &:has(.return-field) {
      padding-top: 96px;
    }
    .return-field {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding: 20px 0px 20px 16px;
      background: #f9f9fa;
      border-radius: 16px 16px 0px 0px;
      button {
        background: none;
        border: none;
        outline: none;
        cursor: pointer;
        font-weight: 600;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.2px;
        text-transform: uppercase;
        display: flex;
        align-items: center;
        color: #727272;
        svg {
          margin-right: 12px;
        }
      }
    }
    .auth-block_header {
      margin-bottom: 32px;
      h4 {
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
      }
      span {
        margin-top: 8px;
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;
      }
    }

    .forgot-btn {
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.2px;
      color: #a7a7a7;
      mix-blend-mode: normal;
      margin-top: 20px;
      padding-left: 12px;
      .forgot-btn_error {
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.2px;
        .error_red {
          color: #f42c4f;
        }
      }
    }
    .common-button {
      font-size: 16px;
      font-weight: 600;
      height: 52px;
      border-radius: 30px;
      margin: 32px 0 16px 0;
    }
    .auth-block_footer {
      display: flex;
      align-items: center;
      span {
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #727272;
        margin-right: 16px;
      }
      button {
        background: none;
        outline: none;
        border: none;
        cursor: pointer;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.2px;
        display: flex;
        align-items: center;
        gap: 12px;
      }
    }
  }
}
